<template>
    <div class="my-setting-r">
        <div class="setting-r-hd">
            <h4 class="my-title_two"></h4>
        </div>
        <div class="setting-r-bd my-setting-get-task">
            <div class="my-info-box" @click="goTaskInfo(data.taskId)">
                <div class="info-box-hd">
                    <h4 class="info-box_title">
                        {{data.name}}
            </h4>
                    <p></p>
                </div>
                <div class="">

                    <p class="info-box_info">{{data.money}}</p>
                    <p class="info-box_info">{{data.address}}</p>

                </div>
                <div class="my-space-Between">
                    <p class="info-box_info">{{data.endTime}} </p>
                    <span class="info-box_info">{{data.createTime}}</span>
                </div>
                <div class="my-space-Between">
                    <p class="info-box_info"></p>
                    <div>
                        <el-button type="primary">
                            

                        </el-button>
                    </div>
                </div>

            </div>
            <div class="my-info-box" @click="goTaskInfo(data.taskId)">
                <div class="info-box-hd">
                    <h4 class="info-box_title">
                        {{data.name}}
            </h4>
                    <p></p>
                </div>
                <div class="">

                    <p class="info-box_info">{{data.money}}</p>
                    <p class="info-box_info">{{data.address}}</p>

                </div>
                <div class="my-space-Between">
                    <p class="info-box_info">{{data.endTime}} </p>
                    <span class="info-box_info">{{data.createTime}}</span>
                </div>
                <div class="my-space-Between">
                    <p class="info-box_info"></p>
                    <div>
                        <el-button type="primary">
                            
                        </el-button>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
  import {mapActions, mapState} from 'vuex'
  import * as GlobalType from '@/store/global/types'
  export default{
    name: 'settingGetTask',
    data(){
      return{
        data: {
          taskId: 0,
          name: '',
          money: 0.5,
          address: '',
          endTime: '2017-05-02 1700',
          createTime: '2017-05-02 1200'
        }

      }
    },
    methods:{
      ...mapActions({
        settingNav: GlobalType.A_SETTING_NAV,
      }),
      goTaskInfo(){
        this.GM_routerPush({
          path: '/intro',
          query: {
            taskId: data.taskId
          }
        })
      }
    },
    mounted(){
      this.settingNav({name:'get_task'})
    },

  }
</script>


