<template>
    <div class="my-intro">
        <div class="my-space-Between">
            <h4 class="my-intro-hd"></h4>
            <el-button type="primary" @click="onPublish"></el-button>

        </div>
        <div class="my-intro-bd">
            <p><i></i>**</p>
            <p><i></i>0.5</p>
            <p><i></i>2017.5.2    1223</p>
            <p><i></i>7</p>
            <p><i></i></p>
            <p><i></i>2017.5.1    1223</p>
        </div>
        <div class="intro-warn"><span></span><span> 0 </span></div>
        <div class="btn-group">
            <el-button type="primary"></el-button>
            <el-button type="danger" @click="onWarn"></el-button>
        </div>
    </div>
</template>
<script>
  export default{
    name: 'intro',
    methods:{
      onPublish:function() {
        
      },
      onWarn:function() {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      }
    }
  }
</script>


