<template>
    <div>
        <el-row class="my-setting">
            <el-col :sm="4">
                <el-menu :router="true" :default-active="settingNav||'info'" class="el-menu-vertical-demo">
                    <el-menu-item index="info"><i class="el-icon-edit"></i></el-menu-item>
                    <el-menu-item index="private"><i class="el-icon-setting"></i></el-menu-item>
                    <el-menu-item index="publish_task"><i class="el-icon-document"></i></el-menu-item>
                    <el-menu-item index="get_task"><i class="el-icon-menu"></i></el-menu-item>
                    <el-menu-item index="community"><i class="el-icon-date"></i></el-menu-item>
                    <el-menu-item index="confirm"><i class="el-icon-warning"></i></el-menu-item>
                </el-menu>
            </el-col>
            <el-col :sm="20">
                <div class="my-setting-r">
                    <router-view></router-view>
                </div>
            </el-col>
        </el-row>

    </div>
</template>
<script>
  import {mapActions, mapState} from 'vuex'
  export default{
    name: 'settingIndex',
    computed:{
      ...mapState({
        settingNav:state=>state.myGlobal.settingNav
      })
    }
  }
</script>


