<template>
    <div class="my-info-box" @click="goTaskInfo(data.taskId)">
        <div class="info-box-hd">
            <h4 class="info-box_title">
                {{data.name}}
            </h4>
            <p></p>
        </div>
        <p class="info-box_info">{{data.money}}</p>
        <p class="info-box_info">{{data.address}}</p>
        <div class="my-space-Between">
            <p class="info-box_info">{{data.endTime}} </p>
            <span class="info-box_info">{{data.createTime}}</span>
        </div>

    </div>
</template>
<script>
  export default{
    name: 'infoBox',
    props: {
      data: {
        type: Object,
        default(){
          return {
            taskId:0,
            name: '',
            money:0.5,
            address:'',
            endTime:'2017-05-02 1700',
            createTime:'2017-05-02 1200'

          }
        }
      }

    },
    methods:{
      goTaskInfo:function(taskId) {
        this.GM_routerPush({
          path: '/intro',
          query: {
            taskId: taskId
          }
        })
      }
    }
  }
</script>



