<template>
    <div class="my-complain j-complain">
        <h4 class="my-complain-hd my-title_two">3</h4>
        <el-row class="my-complain-bd">
            <el-col :sm="12" style="text-align: center">
                <el-form :model="complainFormData" ref="complainForm" :rules="complainRules" label-width="130px">
                    <el-form-item label="" prop="userName">
                        <el-input
                                placeholder=""
                                v-model="complainFormData.userName">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input
                                placeholder=""
                                v-model="complainFormData.phone">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="" prop="userName1">
                        <el-input
                                placeholder=""
                                v-model="complainFormData.userName1">
                        </el-input>
                    </el-form-item>

                    <el-form-item label="" prop="des">
                        <el-input type="textarea"
                                  placeholder=""
                                  v-model="complainFormData.des"
                        >
                        </el-input>
                    </el-form-item>
                </el-form>
                <el-button type="primary"></el-button>
            </el-col>
        </el-row>

    </div>
</template>
<script>
    export default{
      name:'complain',
      data(){
        return {
          complainFormData:{
            phone:'',
            userName:'',
            userName1:'',
            des:''
          },
          complainRules:{
            phone: {
              required: true,
              message: '',
              trigger: 'blur'
            },
            userName1: {
              required: true,
              message: '',
              trigger: 'blur'
            },
            des: {
              required: true,
              message: '',
              trigger: 'blur'
            },

          }
        }
      }
    }
</script>


